class DBSCAN {
  constructor(dataset, epsilon, minPts, distanceFunction) {
    this.dataset = [];
    this.epsilon = 1;
    this.minPts = 2;
    this.distance = this._euclideanDistance;
    this.clusters = [];
    this.noise = [];

    this._visited = [];
    this._assigned = [];
    this._datasetLength = 0;

    this._init(dataset, epsilon, minPts, distanceFunction);
  }

  run = (dataset, epsilon, minPts, distanceFunction) => {
    this._init(dataset, epsilon, minPts, distanceFunction);

    for (let pointId = 0; pointId < this._datasetLength; pointId++) {
      if (this._visited[pointId] !== 1) {
        this._visited[pointId] = 1;

        const neighbors = this._regionQuery(pointId);

        if (neighbors.length < this.minPts) {
          this.noise.push(pointId);
        } else {
          const clusterId = this.clusters.length;
          this.clusters.push([]);
          this._addToCluster(pointId, clusterId);

          this._expandCluster(clusterId, neighbors);
        }
      }
    }
    return this.clusters;
  }

  prettify = () => {
    return this.clusters.map(cluster => {
      return cluster.map(index => ({ x: this.dataset[index][0], y: this.dataset[index][1] }));
    });
  }

  _init = (dataset, epsilon, minPts, distance) => {
    if (dataset) {
      if (!(dataset instanceof Array)) {
        throw Error(`Dataset must be of type array, ${typeof dataset} given`);
      }

      this.dataset = dataset;
      this.clusters = [];
      this.noise = [];

      this._datasetLength = dataset.length;
      this._visited = new Array(this._datasetLength);
      this._assigned = new Array(this._datasetLength);
    }

    if (epsilon) {
      this.epsilon = epsilon;
    }

    if (minPts) {
      this.minPts = minPts;
    }

    if (distance) {
      this.distance = distance;
    }
  }

  _expandCluster = (clusterId, neighbors) => {
    for (let i = 0; i < neighbors.length; i++) {
      const pointId2 = neighbors[i];

      if (this._visited[pointId2] !== 1) {
        this._visited[pointId2] = 1;
        const neighbors2 = this._regionQuery(pointId2);

        if (neighbors2.length >= this.minPts) {
          neighbors = this._mergeArrays(neighbors, neighbors2);
        }
      }
      if (this._assigned[pointId2] !== 1) {
        this._addToCluster(pointId2, clusterId);
      }
    }
  }

  _addToCluster = (pointId, clusterId) => {
    this.clusters[clusterId].push(pointId);
    this._assigned[pointId] = 1;
  }

  _regionQuery = (pointId) => {
    const neighbors = [];

    for (let id = 0; id < this._datasetLength; id++) {
      const dist = this.distance(this.dataset[pointId], this.dataset[id]);
      if (dist < this.epsilon) {
        neighbors.push(id);
      }
    }
    return neighbors;
  }

  _mergeArrays = (a, b) => {
    const len = b.length;

    for (let i = 0; i < len; i++) {
      const P = b[i];
      if (a.indexOf(P) < 0) {
        a.push(P);
      }
    }

    return a;
  }
  _euclideanDistance = (p, q) => {
    let sum = 0;
    let i = Math.min(p.length, q.length);

    while (i--) {
      sum += (p[i] - q[i]) * (p[i] - q[i]);
    }

    return Math.sqrt(sum);
  }
}

export default DBSCAN;
