import React from 'react';
import styles from '../CVCamera.scss';
import GridLoader from "react-spinners/GridLoader";
import { withTheme } from "styled-components";

function CVBackdrop({ show, theme }) {
  if (!show) {
    return null;
  }
  return (
    <div className={styles.backdrop}>
      <GridLoader
        sizeUnit={"px"}
        size={20}
        color={'#123abc'}
        loading={!!show}
        color={theme.color.secondary}
      />
    </div>
  );
}

export default withTheme(CVBackdrop);
