import React, { Component, createRef } from 'react';
import sound from './camera_shutter.mp3';

class Shutter extends Component {
  audioRef = createRef();

  shouldComponentUpdate() {
    return false;
  }

  _invariantCall = () => {
    if (!this.audioRef) {
      console.warn('Audio component unmounted, invoked play');
    }
  };

  play = () => {
    this._invariantCall();
    const playPromise = this.audioRef.current.play();
    if (playPromise !== undefined) {
      playPromise.then(_ => {
        console.log('Shutter Clickd')
      })
      .catch(error => {
        console.log(error)
      });
    }
  };

  render() {
    return (
      <audio
        src={sound}
        preload="true"
        controls="none"
        autoPlay={false}
        style={{ display: 'none' }}
        ref={this.audioRef}
      />
    );
  }
}

export default Shutter;
