/* global cv */
import React, {Component} from 'react';
import get from 'lodash/get';
import If from '/src/If';
import CVBackdrop from './components/CVBackdrop';
import ScanHint from './components/ScanHint';
import CVTakenPhoto from './components/CVTakenPhoto';
import Shutter from './components/Shutter';
import Torch from '../Torch';
import styles from './CVCamera.scss';

import { getSignedUrlForS3, uploadFileToS3, whenCVReady } from './utils';

import { drawViewport, measureImgSize } from './operations/canvas';
import flattenVertices from './operations/flattenVertices';
import {
  getClosestPointsFromCorner,
  getColorsDistance,
  checkContourSize,
  checkIsTrapezoid,
} from './operations/geometry';
import isInViewport from './operations/viewport';
import { getSpecifiedMessageFromOutPoint } from './operations/ui';

import {hints, videoStreamSize, streamResolutions, MAX_MEMORY_LIMIT } from './_cvConstants';

let initialImage = null;

class CVCamera extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const updates = {};
    if (nextProps.open !== prevState.open) {
      updates.open = nextProps.open;

      if (!nextProps.open) {
        updates.isFirstRenderPhase = true;
        updates.takingPhoto = false;
        updates.videoWidth = videoStreamSize.width;
        updates.videoHeight = videoStreamSize.height;
      }
    }
    return updates;
  }

  constructor(props) {
    super(props);

    this.state = {
      isInitialized: false, // is opencv ready to use
      open: !!props.open,
      stream: null,
      track: null, // use to manipulate isTorchOn
      isTorchOn: false,
      takenPhoto: null,
      takingPhoto: false,

      countOfCaptures: 0,
      videoWidth: videoStreamSize.width,
      videoHeight: videoStreamSize.height,
      hideCaptureButton: true,
      isFirstRenderPhase: true,
    };

    this.videoRef = React.createRef();
    this.canvasRef = React.createRef();
    this.hiddenCanvasRef = React.createRef();


    // Track center coordinate changes;
    this.requestAnimationFrameId = null;
    this.autoCaptureOptions = {
      detectionStarted: false,
      captureTimeoutId: null,
      contourCenter: {
        x: 0,
        y: 0,
      },
      radius: 200,
    };

    this.documentColorRangeOptions = {
      isInColorRange: false,
      maxColorDistance: 200,
      expectedDocumentColor: { r: 255, g: 255, b: 255 }, // white
    };

    this.canvasViewPortSize = 30;
    this.canvasViewPortTopAdditionalSize = 20;
    this.cntArea = 0;
    this.autocaptureConditions = {
      isContourMissing: false,
      isCenterMoved: false,
      isColorOutOfRange: false,
      areVerticesOutFromViewport: false,
      isSizeSmall: false,
      isShapeTrapezoid: false,
      cornersCountIsNotFour: false,
    };

    this.filteredVertices = [];

    this.scanHintRef = React.createRef();
    this.shutterRef = React.createRef();
  }

  async componentDidUpdate(prevProps) {
    const { isInitialized, stream } = this.state;

    if (this.state.open !== prevProps.open && this.state.open) {
      await this.start();
    }

    if (this.state.open && isInitialized && stream) {
      this.draw();
    }
  }

  componentWillUnmount() {
    const { track } = this.state;
    if (!track) {
      return;
    }
    this.props.closeCamera({ track });
  }

  get shouldPlay() {
    const {
      open,
      stream,
      isInitialized,
      isFirstRenderPhase,
    } = this.state;

    return open && stream && isInitialized && !isFirstRenderPhase;
  }

  start = async () => {
    if (!this.state.isInitialized) {
      await whenCVReady();
      return new Promise((resolve) => {
        this.setState({ isInitialized: true }, () => {
          this.getUserStream().then(resolve);
        });
      });
    }

    return this.getUserStream();
  };

  getUserStream = () => {
    if (!navigator.mediaDevices) {
      return Promise.reject();
    }
    if (get(this.state.stream, 'active')) {
      return Promise.resolve(this.state.stream);
    }

    const mediaConstraints = {
      video: {
        facingMode: 'environment',
        width: { ideal: 1280, max:2560 },
        height: { ideal: 720, max: 1440 },
      },
    };

    return navigator.mediaDevices
      .getUserMedia(mediaConstraints)
      .then(this.onStream)
      .catch(this.onStreamError);
  };

  onStream = async (stream) => {
    const track = stream.getVideoTracks()[0];

    await this.playStreamOnVideo(stream);

    return new Promise(resolve => {
      this.setState({ stream, track }, resolve);
    });
  };

  onStreamError = (reason) => {
    const reasonToString = reason && reason.toString();
    const lowerCaseReason = reasonToString && reasonToString.toLowerCase();
    const isPermissionDenied = lowerCaseReason && lowerCaseReason.includes('permission denied');
    this.props.closeCVCamera();
    if (isPermissionDenied) {
      localStorage.setItem('permissionDenied', 'true');
    }
  };

  draw = () => {
    if (!this.state.open || !this.canvasRef.current) {
      return;
    }
    if (this.state.takingPhoto || this.state.takenPhoto) {
      return;
    }
    const video = this.videoRef.current;
    const {width, height} = video;

    Object.keys(this.autocaptureConditions).forEach((key) => this.autocaptureConditions[key] = false);

    this.filteredVertices.length = 0;

    initialImage = new cv.Mat(height, width, cv.CV_8UC4);

    const cap = new cv.VideoCapture(video);

    cap.read(initialImage);


    this.getLargestContour();

    const isDocumentDetected = !Object.keys(this.autocaptureConditions)
      .filter(key => this.autocaptureConditions[key] === true && key !== 'isCenterMoved')
      .length;

    cv.imshow(this.canvasRef.current, initialImage);

    // If document is detected draw shade on it
    const { filteredVertices } = this;
    if (isDocumentDetected && filteredVertices.length) {
      const canvas = this.canvasRef.current;
      const ctx = canvas.getContext('2d');
      ctx.beginPath();
      ctx.fillStyle = '#e7c020'; // Document Shade Color
      filteredVertices.forEach((vertex, index) => {
        if (index === 0) {
          ctx.moveTo(vertex.x, vertex.y);
          return;
        }
        ctx.lineTo(vertex.x,vertex.y);
      });
      ctx.lineTo(filteredVertices[0].x, filteredVertices[0].y);
      ctx.closePath();
      ctx.globalAlpha = 0.5; // Document shade opacity
      ctx.fill();
    }

    drawViewport({
      canvas: this.canvasRef.current,
      canvasViewPortSize: this.canvasViewPortSize,
      canvasViewPortTopAdditionalSize: this.canvasViewPortTopAdditionalSize,
    });

    initialImage.delete();

    if (this.state.isFirstRenderPhase) {
      this.setState({ isFirstRenderPhase: false }, () => {
        this.requestAnimationFrameId = requestAnimationFrame(this.draw);
      });
      return;
    }
    this.requestAnimationFrameId = requestAnimationFrame(this.draw);
  };

  getLargestContour = () => {
    const {rows: initialImageRows, cols: initialImageCols} = initialImage;
    const initialImageType = initialImage.type();

    // Step 1. Make image gray
    const gray = new cv.Mat(initialImageRows, initialImageCols, initialImageType);
    cv.cvtColor(initialImage, gray, cv.COLOR_BGR2GRAY);

    // Step 2. Apply GaussianBlur
    const ksize = {width: 3, height: 3};
    cv.GaussianBlur(gray, gray, ksize, 0);

    const mask = cv.Mat.zeros(gray.rows, gray.cols, gray.type());

    // Step 3. Dilate
    const dilated = new cv.Mat();
    const MORPH = 3;
    const structuringElementSize = {width: MORPH, height: MORPH};
    const kernel = cv.getStructuringElement(cv.MORPH_RECT, structuringElementSize);
    cv.dilate(gray, dilated, kernel);

    gray.delete();

    // Step 4. Apply Canny Edge Detection
    const edged = new cv.Mat();
    const threshold = new cv.Mat();
    const CannyAccThresh = cv.threshold(dilated,threshold,0,255,cv.THRESH_BINARY + cv.THRESH_OTSU);
    threshold.delete();

    const CannyThresh = 0.1 * CannyAccThresh;
    cv.Canny(dilated, edged, CannyThresh, CannyAccThresh);
    dilated.delete();

    // Step 5. Find Contours
    const contours = new cv.MatVector();
    const hierarchy = new cv.Mat();
    cv.findContours(edged, contours, hierarchy, cv.RETR_LIST, cv.CHAIN_APPROX_SIMPLE);

    edged.delete();
    hierarchy.delete();

    let largestApprox = null;
    let largestArea = 0;

    const onlyContours = new cv.Mat(initialImage.rows, initialImage.cols, initialImage.type());
    const contoursSize = contours.size();
    for (let i = 0; i < contoursSize; ++i) {
      const cnt = contours.get(i);

      // Step 6. Approximate each contour
      const epsilon = 0.01 * cv.arcLength(cnt, true);
      const approx = new cv.Mat();
      /**
       * Last argument is showing is it closed
       *  If true, the approximated curve is closed its first and last vertices are connected. Otherwise, it is not closed.
       */
      cv.approxPolyDP(cnt, approx, epsilon, true);

      cnt.delete();

      const approxTotal = approx.total();

      const approxArea = cv.contourArea(approx);
      // find contour with largest area
      if (approxTotal >= 4 && approxArea >= largestArea) {
        largestArea = approxArea;
        largestApprox = approx;
      }

      // approx.delete(); can't delete this Uncaught: BindingError: message: "Cannot pass deleted object as a pointer of type Mat"
    }
    const vertices = [];

    let hintMessage = null;
    if (largestApprox) {
      const maskContourColor = [255, 255, 0, 0];
      const contourColor = [231, 192, 32, 255];


      const cntSize = largestApprox.size().width * largestApprox.size().height;
      this.cntArea = cv.contourArea(largestApprox);
      for (let j = 0; j < cntSize; j++) {
        const [x, y] = largestApprox.intPtr(j);
        const vertex = {x, y};
        vertices.push(vertex);
      }

      // Step 7. Apply convexHull to close the contour
      const hull = new cv.Mat();
      cv.convexHull(largestApprox, hull, false, true);
      const contoursToDraw = new cv.MatVector();
      contoursToDraw.push_back(hull);


      // find contour center coordinates
      const moments = cv.moments(largestApprox);
      const { m10, m00, m01 } = moments;
      const currentX = parseInt(m10 / m00, 10);
      const currentY = parseInt(m01 / m00, 10);

      // calculate distance between current and previous contour centers
      const { x: previousX, y: previousY } = this.autoCaptureOptions.contourCenter;
      const dx = currentX - previousX;
      const dy = currentY - previousY;
      const distance = this.calcDistance(dx, dy);

      // check whether the distance is too long
      this.autocaptureConditions.isCenterMoved = distance > this.autoCaptureOptions.radius;

      // set new center coordinates
      this.autoCaptureOptions.contourCenter.x = currentX;
      this.autoCaptureOptions.contourCenter.y = currentY;



      largestApprox.delete();
      hull.delete();

      // Step8. Draw the contour on the mask
      cv.drawContours(mask, contoursToDraw, -1, maskContourColor, cv.FILLED, cv.LINE_AA);
      // Drawing the contour on the image
      cv.drawContours(onlyContours, contoursToDraw, -1, contourColor, cv.FILLED, cv.LINE_AA);

      contoursToDraw.delete();
    } else {
      hintMessage = hints.general;
      this.autocaptureConditions.isContourMissing = true;
    }

    contours.delete();

    // contoursWithShade the detected contours from the image
    const contoursWithShade = new cv.Mat(initialImageRows, initialImageCols, cv.CV_8UC3);
    onlyContours.copyTo(contoursWithShade, mask);


    // From the initial image crop the contour part
    const maskArea = new cv.Mat(initialImageRows, initialImageCols, initialImageType);
    initialImage.copyTo(maskArea, mask);

    // get average color of the detected contour
    const averageColor = cv.mean(maskArea, mask);
    const r = averageColor[0];
    const g = averageColor[1];
    const b = averageColor[2];

    // calculate difference of average color with expected color
    const diff = getColorsDistance(this.documentColorRangeOptions.expectedDocumentColor, { r, g, b });

    this.autocaptureConditions.isColorOutOfRange = diff >= this.documentColorRangeOptions.maxColorDistance;


    const { coordinates, scales } = measureImgSize({
      canvas: this.canvasRef.current,
      canvasViewPortSize: this.canvasViewPortSize,
      canvasViewPortTopAdditionalSize: this.canvasViewPortTopAdditionalSize,
    });

    const clusters = flattenVertices(vertices);
    const filteredVertices = getClosestPointsFromCorner(
      clusters,
      { rows: initialImageRows, cols: initialImageCols},
      this.autoCaptureOptions.contourCenter
    );

    this.filteredVertices = [...filteredVertices];
    const { isOut, inViewport, outViewport } = isInViewport(filteredVertices, coordinates, initialImage);

    this.autocaptureConditions.areVerticesOutFromViewport = isOut;
    hintMessage = hintMessage || getSpecifiedMessageFromOutPoint(outViewport);

    const {
      cntArea,
      canvasViewPortSize,
      canvasViewPortTopAdditionalSize,
    } = this;

    const sizes = {
      cntArea,
      viewPortSize: canvasViewPortSize,
      viewPortTopAdditionalSize: canvasViewPortTopAdditionalSize,
      canvasFullWidth: initialImageCols,
      canvasFullHeight: initialImageRows,
      scales,
    };

    const { isContourSizeSmall, isContourSizeSmallMessage } = checkContourSize(sizes, 100, 100);
    this.autocaptureConditions.isSizeSmall = isContourSizeSmall;

    hintMessage = hintMessage || isContourSizeSmallMessage;

    const { isTrapezoid, isTrapezoidMessage } = checkIsTrapezoid(inViewport);
    this.autocaptureConditions.isShapeTrapezoid = isTrapezoid;
    hintMessage = hintMessage || isTrapezoidMessage;

    if (filteredVertices.length !== 4) {
      this.autocaptureConditions.cornersCountIsNotFour = true;
      hintMessage = hintMessage || hints.general;
    }

    if (hintMessage) {
      this.scanHintRef.current.setMessage(hintMessage);
    } else {
      this.scanHintRef.current.hideHint();
    }


    const shouldStopAutoCapture = Object.keys(this.autocaptureConditions)
      .filter(key => this.autocaptureConditions[key] === true)
      .length;

    if (shouldStopAutoCapture) {
      this.stopAutomaticallyCapturingProcess();
    }

    this.startAutomaticallyCapturingProcess();

    // All cv.Mat()-s should be deleted if we don't use them anymore
    mask.delete();
    onlyContours.delete();
    maskArea.delete();
    contoursWithShade.delete();
  };

  // used to calculate distance between previous and next contours center
  calcDistance(a, b) {
    return (Math.sqrt((a * a) + (b * b)));
  }

  startAutomaticallyCapturingProcess = () => {
    if (this.autoCaptureOptions.detectionStarted) {
      return;
    }
    this.autoCaptureOptions.detectionStarted = true;
    this.autoCaptureOptions.captureTimeoutId = setTimeout(() => {
      this.prepareToTakePhoto();
    }, 1000);
  };

  stopAutomaticallyCapturingProcess = () => {
    this.autoCaptureOptions.detectionStarted = false;
    clearTimeout(this.autoCaptureOptions.captureTimeoutId);
  };

  playStreamOnVideo = (stream) => {
    const potentialDrawDelayMs = 700; // ~1FPS

    return new Promise((resolve) => {
      this.videoRef.current.srcObject = stream;
      this.videoRef.current.onplay = () => setTimeout(resolve, potentialDrawDelayMs);
      this.videoRef.current.play();
    });
  };

  switchTorch = () => {
    const { isTorchOn, track, stream } = this.state;
    if (stream)
      this.setState({
        isTorchOn: !isTorchOn,
      }, () => this.state.isTorchOn ? this.turnOnTorch(track) : this.turnOffTorch(track));
  };

  turnOnTorch = (track) => {
    track.applyConstraints({
      advanced: [{ torch: true }],
    }).then(_ => console.log('Started Torch')).catch(err => console.log(err));
  };

  turnOffTorch = (track) => {
    track.stop();
    this.setState({
      stream: null,
    },() => this.start());
  };

  /**
   * Change resolution
   */
  prepareToTakePhoto = async () => {
    if (!this.state.open) {
      return;
    }
    this.setState({ takingPhoto: true });
    const shouldChangeResolution = performance.memory && performance.memory.usedJSHeapSize < MAX_MEMORY_LIMIT;
    if (shouldChangeResolution) {
      await this.setStreamMaxResolution();
    }

    await this.setVideoTagMaxResolution();

    this.takePhotoAction();
  };

  setStreamMaxResolution = () => {
    const { track } = this.state;
    const capabilities = track.getCapabilities();
    const { width, height } = capabilities;
    const maxWidth = width && width.max;
    const maxHeight = height && height.max;
    // TODO remove this console
    // eslint-disable-next-line
    console.log(`Device max Width = ${maxWidth} & max Height = height`);

    const {
      width: acceptableWidth,
      height: acceptableHeight,
    } = this.getMaxAcceptableResolution({
      deviceMaxWidth: maxWidth,
      deviceMaxHeight: maxHeight,
    });
    const constraints = {
      width: { ideal: acceptableWidth },
      height: { ideal: acceptableHeight },
    };

    return track.applyConstraints(constraints);
  };

  setVideoTagMaxResolution = () => {
    return new Promise(resolve => {
      const { track } = this.state;
      const settings = track.getSettings();
      const { width: streamWidth, height: streamHeight } = settings;
      const isPortrait = window.innerHeight > window.innerWidth;
      const minLength = streamWidth >= streamHeight ? streamHeight : streamWidth;
      const maxLength = streamWidth >= streamHeight ? streamWidth : streamHeight;
      // TODO remove this console
      // eslint-disable-next-line
      console.log(`Photo Width=${maxLength} & Height=${minLength}`);

      this.setState({
        videoWidth: isPortrait ? minLength : maxLength,
        videoHeight: isPortrait ? maxLength : minLength,
      }, resolve);
    });
  };

  getMaxAcceptableResolution = ({ deviceMaxWidth, deviceMaxHeight }) => {
    const maxAcceptableResolution = { width: 0, height: 0 };

    const acceptableResolutions = streamResolutions
      .filter(resolution => deviceMaxWidth > resolution.width && deviceMaxHeight > resolution.height);

    if (!acceptableResolutions.length) {
      maxAcceptableResolution.width = deviceMaxWidth;
      maxAcceptableResolution.height = deviceMaxHeight;
    }
    maxAcceptableResolution.width = acceptableResolutions[0].width;
    maxAcceptableResolution.height = acceptableResolutions[0].height;

    return maxAcceptableResolution;
  };

  takePhotoAction = () => {
    const { countOfCaptures, track } = this.state;
    cancelAnimationFrame(this.requestAnimationFrameId);
    if (track.readyState !== 'live') {
      return;
    }
    const {docType='unknown'} = this.props;
    const suffixOfFileName = docType.charAt(0).toUpperCase() + docType.slice(1);
    const video = this.videoRef.current;
    const {width, height} = video;

    initialImage = new cv.Mat(height, width, cv.CV_8UC4);

    const cap = new cv.VideoCapture(video);
    cap.read(initialImage);

    track.stop();

    cv.imshow(this.canvasRef.current, initialImage);

    this.cropViewport();
    initialImage.delete();

    this.shutterRef.current.play();
    this.hiddenCanvasRef.current.toBlob((blob) => {
      const name = `${suffixOfFileName}${countOfCaptures}`;
      blob.name = name;
      this.setState({
        takenPhoto: blob,
        isTorchOn: false,
        countOfCaptures: countOfCaptures + 1,
        takingPhoto: false,
        videoWidth: videoStreamSize.width,
        videoHeight: videoStreamSize.height,
      }, this.stopAutomaticallyCapturingProcess);
    }, 'image/jpeg', 100);
  };

  cropViewport() {
    const sourceCanvas = this.canvasRef.current;


    const viewportCanvas = this.hiddenCanvasRef.current;
    const viewportCtx = viewportCanvas.getContext('2d');

    const { coordinates, size } = measureImgSize({
      canvas: sourceCanvas,
      canvasViewPortSize: this.canvasViewPortSize,
      canvasViewPortTopAdditionalSize: this.canvasViewPortTopAdditionalSize,
    });
    const { top, left } = coordinates;
    const { viewportWidth, viewportHeight } = size;

    viewportCanvas.width = viewportWidth;
    viewportCanvas.height = viewportHeight;

    viewportCtx.drawImage(
      sourceCanvas,
      left,
      top,
      viewportWidth,
      viewportHeight,
      0,
      0,
      viewportWidth,
      viewportHeight
    );
  }

  retakePhoto = () => {
    this.setState({ takenPhoto: null }, this.start);
  };

  usePhoto = () => {
    const { takenPhoto, track } = this.state;
    const {
      setProgress,
      closeCamera,
      onFinish,
      onError,
    } = this.props;
    closeCamera({ track });
    setProgress(0, 'Waiting', takenPhoto);

    // getSignedUrlForS3({ file: takenPhoto })
    //   .then((res) => {
    //     const { data: { signedUrl, key, filename } } = res;
    //     uploadFileToS3({ signedUrl, file: takenPhoto, setProgress})
    //       .then(() => {
    //         this.setState({
    //           takenPhoto: null,
    //         });
    //         onFinish({
    //           filename,
    //           key,
    //           signedUrl,
    //         },takenPhoto);
    //       });
    //   }).catch(err => onError(err));
  };

  cancel = () => {
    const { closeCamera } = this.props;
    const { track } = this.state;
    closeCamera({ openFileUploader: true, track });
  };

  renderLiveStream = () => {
    const { isTorchOn, hideCaptureButton } = this.state;

    const isReady = this.shouldPlay;

    return (
      <React.Fragment>
        <div className={styles.riseZIndex} style={{ display: (isReady) ? 'block' : 'none' }}>
          <div className={styles.canvasContainer}>
            <canvas ref={this.canvasRef} className={styles.cvCamera}/>
            <ScanHint ref={this.scanHintRef} />
          </div>
          <div className={styles.headPart}>
            <div className={styles.cancelButton} onClick={this.cancel}>Cancel</div>
            <Torch on={isTorchOn} switchTorch={this.switchTorch}/>
          </div>
          <div onClick={this.prepareToTakePhoto} className={styles.whiteBorder} style={{ display: (hideCaptureButton) ? 'none' : 'block' }}>
            <div className={styles.whiteCircle}></div>
          </div>
          <div className={styles.underLine} style={{ display: (hideCaptureButton) ? 'none' : 'block' }} />
        </div>
        <CVBackdrop show={!isReady} />
      </React.Fragment>
    );
  };

  render() {
    const { open } = this.state;
    const { takenPhoto, videoWidth, videoHeight } = this.state;
    const isTakenPhotoScreenReady = !!takenPhoto;

    const shouldRenderTakenPhoto = open && isTakenPhotoScreenReady;

    return (
      <div style={{ backgroundColor: 'black' }}>
        <video
          width={videoWidth}
          height={videoHeight}
          autoPlay
          ref={this.videoRef}
          className={styles.video}
          style={{ display: (open) ? 'block' : 'none' }} // fix for iPhone
          playsInline={true}
        />
        <canvas ref={this.hiddenCanvasRef} className={styles.hideCanvas} />
        <Shutter ref={this.shutterRef} />
        <If condition={open}>
          <If.True>
            {this.renderLiveStream()}
          </If.True>
        </If>
        <If condition={shouldRenderTakenPhoto}>
          <If.True>
            <CVTakenPhoto
              img={takenPhoto}
              onRetake={this.retakePhoto}
              onUsePhoto={this.usePhoto}
            />
          </If.True>
        </If>
      </div>
    );
  }
}

export default CVCamera;
