import React from 'react';
import styles from './Torch.scss';

export default function Index(props) {
  return (
    !navigator.userAgent.match(/(iPad|iPhone|iPod)/g) ?
      <div className={styles.torch} onClick={props.switchTorch}>
        <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 20 20" fill={props.on ? '#FFAF00' : '#FFFFFF'}>
          <path d="M0 0h24v24H0z" fill="none"/>
          <path d="M7 2v11h3v9l7-12h-4l4-8z"/>
        </svg>
      </div> : null
  );
}
