const getXsStyles = () => ({
  overlay: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(74, 74, 74, 0.9)',
    zIndex: '10000',
  },
  content: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    border: 'none',
    backgroundColor: '#fff',
    overflow: 'scroll',
    WebkitOverflowScrolling: 'touch',
    borderRadius: 'none',
    outline: 'none',
    padding: 0,
  },
});

export default function(viewport) {
  const xsStyles = getXsStyles();
  if (viewport === 'xs') {
    return xsStyles;
  }
  return {
    overlay: xsStyles.overlay,
    content: {
      ...xsStyles.content,
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      width: '700px',
      maxHeight: '90vh',
      overflow: 'visible',
      WebkitTransform: 'translate(-50%, -50%)',
      MsTransform: 'translate(-50%, -50%)',
      transform: 'translate(-50%, -50%)',
      backgroundColor: '#fff',
    },
  };
}
