import _assign from 'lodash/assign';

const baseFilename = filename => filename.lastIndexOf('.') >= 0 ? /.*(?=\.)/.exec(filename)[0] : filename;
const fileExtension = filename => filename.lastIndexOf('.') >= 0 ? /[^.]*$/.exec(filename)[0] : '';

/**
 * This function is used to generate the file name we'll use to display it to the user
 * If the user upload a file image.png, they'll see image.png
 * But if a file with the same name is uploaded again, it'll become image_(1).png, followed by image_(2).png
 * This is needed when we upload files using the camera on mobile, since all the uploaded files have the same name
 */
export const getFileDisplayName = (uploadedFiles, newFilename) => {
  // If none of the uploaded files have the same name as the new file, return the new file's name
  if ((uploadedFiles || []).every(f => f.originalName !== newFilename)) return newFilename;
  // Otherwise, we need to figure out what the display name should be
  const fileNumbers = uploadedFiles.filter(f => f.originalName === newFilename).map(f => parseInt(f.displayName.replace(/\D/g, ''))).filter(f => f);
  const maxFileNumber = fileNumbers.length ? Math.max.apply(Math, fileNumbers) : 0;
  return `${baseFilename(newFilename)}_(${maxFileNumber + 1}).${fileExtension(newFilename)}`;
};

export const modFileHandle = (fileHandle, uploadedFiles = []) => {
  const originalName = fileHandle.file.name;
  return _assign({}, fileHandle, {
    originalName,
    displayName: getFileDisplayName(uploadedFiles, originalName),
  });
};

export const appendUploadedFile = (uploadedFiles, fileHandle) => {
  return (uploadedFiles || []).concat(modFileHandle(fileHandle, uploadedFiles));
};
