import DBSCAN from './lib/DBSCAN';

const flattenVertices = (vertices) => {
  const dataset = vertices.map((vertice) => [vertice.x, vertice.y]);

  const dbscan = new DBSCAN();
  dbscan.run(dataset, 30, 1); // Second param is radius
  const clusters = dbscan.prettify();

  return clusters;
};

export default flattenVertices;
