import React, {PureComponent} from 'react';
import styles from '../CVCamera.scss';
import { hints } from '../_cvConstants';

class ScanHint extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      message: hints.center,
    };
    this.hintTimeoutId = null;
  }

  /**
   *  This function used to pass text to this componen without calling setState inside CVCamera
   * */
  setMessage = (message) => {
    if (this.state.message.text === message.text) {
      return;
    }

    if (!message.text) {
      return this._hideHint();
    }
    this._startHintTimeout(message);
  };


  hideHint = () => this.setMessage({ text: null, icon: null });

  /**
   * @private
   * */
  _startHintTimeout = (message) => {
    if (this.hintTimeoutId ) {
      return;
    }
    this.hintTimeoutId = setTimeout(() => {
      this.setState({ message } , this._startHideTimeout);
    }, 1000);
  };

  /**
   * @private
   * */
  _clearHintTimeout = () => {
    clearTimeout(this.hintTimeoutId);
    this.hintTimeoutId = null;
  };

  /**
   * @private
   * */
  _hideHint = () => {
    this._clearHideTimeout();
    this._clearHintTimeout();
    this.setState({
      message: {
        text: '',
        icon: null,
      },
    });
  };

  _startHideTimeout = () => {
    if (this.hideTimeoutId) {
      return;
    }
    this.hideTimeoutId = setTimeout(this.hideHint, 3000);
  }

  _clearHideTimeout = () => {
    clearTimeout(this.hideTimeoutId);
    this.hideTimeoutId = null;
  }


  render() {
    const { message } = this.state;
    return (
      <div className={styles.container}>
        <div className={styles.hintContainer} style={{ display: message.text ? 'block' : 'none' }} >
          {message.text}
          {/*{message.icon && message.icon}*/}
        </div>
      </div>
    );
  }
}

export default ScanHint;
