import { hints } from '../_cvConstants';

const getDistance = (point1, point2) => {
  const dx = point1.x - point2.x;
  const dy = point2.y - point2.y;
  const distanceSquare = dx * dx + dy * dy;
  const distance = Math.sqrt(distanceSquare);
  return distance;
};

const detectQuadrant = (vertex, center) => {
  if (vertex.x < center.x && vertex.y < center.y) {
    return 'topLeft';
  }
  if (vertex.x > center.x && vertex.y < center.y) {
    return 'topRight';
  }
  if (vertex.x > center.x && vertex.y > center.y) {
    return 'bottomRight';
  }
  if (vertex.x < center.x && vertex.y > center.y) {
    return 'bottomLeft';
  }
  return 'topLeft'; // it's center so does not make sense which corner
};

const getClosestPointsFromCorner = (clusters, {rows, cols}, center) => {
  const corners = {
    topLeft: { x: 0, y: 0 },
    topRight: { x: cols, y: 0 },
    bottomRight: { x: cols, y: rows },
    bottomLeft: { x: 0, y: rows },
  };
  const vertices = [];
  clusters.forEach((cluster) => {
    const closestPoint = {
      distance: Infinity,
      index: -1,
    };

    cluster.forEach((point, index) => {
      const quadrant = detectQuadrant(point, center);

      const distance = getDistance(point, corners[quadrant]);
      if (distance < closestPoint.distance) {
        closestPoint.distance = distance;
        closestPoint.index = index;
      }
    });

    vertices.push(cluster[closestPoint.index]);
  });
  return vertices;
};

const getOpposite = (side) => {
  const sideOposites = {
    left: 'right',
    right: 'left',
    top: 'bottom',
    bottom: 'top',
  };

  return sideOposites[side];
};

const checkContourSize = (sizes) => {
  const {
    cntArea,
    viewPortSize,
    viewPortTopAdditionalSize,
    canvasFullWidth,
    canvasFullHeight,
    scales,
  } = sizes;

  const { scaleWidth, scaleHeight } = scales;

  const rectangleWidth = canvasFullWidth - 2 * viewPortSize * scaleWidth;
  const rectangleHeight = canvasFullHeight - 2 * viewPortSize * scaleHeight - viewPortTopAdditionalSize * scaleHeight;
  const rectangleSurfaceArea = rectangleWidth * rectangleHeight;

  const maxSurfaceAreaDiff = rectangleSurfaceArea * 0.5;

  const isSurfaceAreaSmall = Math.abs(rectangleSurfaceArea - cntArea) >= maxSurfaceAreaDiff;
  const isContourSizeSmall = isSurfaceAreaSmall;
  const isContourSizeSmallMessage = isContourSizeSmall ? hints.zoomIn : null;
  return { isContourSizeSmall, isContourSizeSmallMessage };
};

const getColorsDistance = (color1, color2) => {
  return Math.sqrt(
    Math.pow(color1.r - color2.r, 2) + Math.pow(color1.g - color2.g, 2) + Math.pow(color1.b - color2.b, 2));
};

const getLineCoefficient = (point1, point2) => {
  const dx = point1.x - point2.x;
  const dy = point1.y - point2.y;
  if (dx === 0 || dy === 0) {
    return 0;
  }
  const k = dy / dx;
  return k;
};

const checkIsTrapezoid = (vertices) => {
  const verticesAmount = vertices.length;
  if (verticesAmount !== 4) {
    return false;
  }

  const segments = [];
  const lineCoefficents = [];
  vertices.forEach((vertex, index) => {
    const nextVertexIndex = index === verticesAmount - 1 ? 0 : index + 1;
    const segment = getDistance(vertex, vertices[nextVertexIndex]);
    segments.push(segment);
    const lineCoefficent = getLineCoefficient(vertex, vertices[nextVertexIndex]);
    lineCoefficents.push(Math.abs(lineCoefficent));
  });

  const segmentsDiff1 = Math.abs(segments[0] - segments[2]);
  const segmentsDiff2 = Math.abs(segments[1] - segments[3]);

  const dk1 = Math.abs(lineCoefficents[0] - lineCoefficents[2]);
  const dk2 = Math.abs(lineCoefficents[1] - lineCoefficents[3]);


  let isTrapezoid = false;
  if (segmentsDiff1 > 70 || (dk1 > 0.5 && segmentsDiff1 > 20)) {
    isTrapezoid = true;
  }
  if (segmentsDiff2 > 70 || (dk2 > 0.5 && segmentsDiff2 > 20)) {
    isTrapezoid = true;
  }

  const isTrapezoidMessage = isTrapezoid ? hints.rotate : null;

  return { isTrapezoid, isTrapezoidMessage };
};


export {
  getDistance,
  getClosestPointsFromCorner,
  detectQuadrant,
  getOpposite,
  checkContourSize,
  checkIsTrapezoid,
  getColorsDistance,
};
