import React, { Children, cloneElement } from 'react';

const True = ({ condition, children }) => (condition ? children : null);

const False = ({ condition, children }) => (condition ? null : children);

function Index({ condition, children }) {
  return Children.map(children, (child) => {
    return cloneElement(child, { condition });
  });
}

Index.True = True;
Index.False = False;

export default Index;
