import axios from 'axios';

export const getSignedUrlForS3 = (settings) => {
  const { file } = settings;
  return axios.get(`/s3/sign?objectName=${file.name}.jpg&contentType=${file.type}`);
};

export const uploadFileToS3 = (settings) => {
  const { signedUrl, file, setProgress} = settings;
  const options = {
    headers: {
      'Content-Type': file.type,
      'x-amz-acl': 'private',
    },
    onUploadProgress: progressEvent => {
      const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
      setProgress(percentCompleted, percentCompleted === 100 ? 'Finalizing' : 'Uploading', file);
    },
  };
  return axios.put(signedUrl, file, options);
};


