const isInViewport = (vertices, coords, initialImage) => {
  const { top, left, right, bottom } = coords;
  const { rows, cols } = initialImage;


  return vertices.reduce((prev, {x, y}) => {
    const isTopOut = y < top;
    const isLeftOut = x < left;
    const isBottomOut = (rows - y) < bottom;
    const isRightOut = (cols - x) < right;

    const outstandingCorner = {
      isTopOut,
      isLeftOut,
      isBottomOut,
      isRightOut,
    };

    const _isOut = isTopOut || isLeftOut || isBottomOut || isRightOut;
    const inViewport = _isOut
      ? []
      : [{x, y}];

    const outViewport = _isOut
      ? [{x, y, result: outstandingCorner}]
      : [];

    return {
      isOut: prev.isOut || _isOut,
      inViewport: [...prev.inViewport, ...inViewport],
      outViewport: [...prev.outViewport, ...outViewport],
    };
  }, { isOut: false, inViewport: [], outViewport: [] });
};

export default isInViewport;
