const measureImgSize = ({
  canvas,
  canvasViewPortSize,
  canvasViewPortTopAdditionalSize,
}) => {
  const {
    width,
    clientWidth,
    height,
    clientHeight,
  } = canvas;

  const canvasViewPortTopSize = canvasViewPortSize + canvasViewPortTopAdditionalSize;
  const scaleWidth = width / clientWidth;
  const scaleHeight = height / clientHeight;
  const scaledViewPortSizeTop = canvasViewPortTopSize * scaleHeight;
  const scaledViewPortSizeBottom = canvasViewPortSize * scaleHeight;
  const scaledViewPortSizeHorizontal = canvasViewPortSize * scaleWidth;

  const viewportWidth = width - 2 * scaledViewPortSizeHorizontal;
  const viewportHeight = height - scaledViewPortSizeTop - scaledViewPortSizeBottom;

  const coordinates = {
    top: scaledViewPortSizeTop,
    left: scaledViewPortSizeHorizontal,
    bottom: scaledViewPortSizeBottom,
    right: scaledViewPortSizeHorizontal,
  };
  const scales = {
    scaleWidth,
    scaleHeight,
  };

  const size = {
    viewportWidth,
    viewportHeight,
  };

  return {
    coordinates,
    scales,
    size,
  };
};

const drawViewport = ({
  canvas,
  canvasViewPortSize,
  canvasViewPortTopAdditionalSize,
}) => {
  const {width, clientWidth, height, clientHeight} = canvas;
  const canvasViewPortTopSize = canvasViewPortSize + canvasViewPortTopAdditionalSize;
  const ctx = canvas.getContext('2d');
  ctx.fillStyle = '#000000';
  ctx.globalAlpha = 0.5;

  /**
   * Canvas element is scaled, so we need to scale sizes too
   */
  const scaleWidth = width / clientWidth;
  const scaleHeight = height / clientHeight;

  ctx.fillRect(
    0 * scaleWidth,
    0 * scaleHeight,
    clientWidth * scaleWidth,
    canvasViewPortTopSize * scaleHeight,
  ); // TOP line
  ctx.fillRect(
    0 * scaleWidth,
    canvasViewPortTopSize * scaleHeight,
    canvasViewPortSize * scaleWidth,
    (clientHeight - 2 * canvasViewPortSize - canvasViewPortTopAdditionalSize) * scaleHeight,
  ); // Left line
  ctx.fillRect(
    0 * scaleWidth,
    (clientHeight - canvasViewPortSize) * scaleHeight,
    clientWidth * scaleWidth,
    canvasViewPortSize * scaleHeight,
  ); // Bottom line
  ctx.fillRect(
    (clientWidth - canvasViewPortSize) * scaleWidth,
    canvasViewPortTopSize * scaleHeight,
    canvasViewPortSize * scaleWidth,
    (clientHeight - 2 * canvasViewPortSize - canvasViewPortTopAdditionalSize) * scaleHeight,
  );

  ctx.globalAlpha = 1.0;

  const leftX = canvasViewPortSize * scaleWidth;
  const topY = (canvasViewPortSize + canvasViewPortTopAdditionalSize) * scaleHeight;
  const rightX = (clientWidth - canvasViewPortSize) * scaleWidth;
  const bottomY = (clientHeight - canvasViewPortSize) * scaleHeight;

  const cornerSize = 20;
  const cornerWidthSize = cornerSize * scaleWidth;
  const cornerHeightSize = cornerSize * scaleHeight;
  const cornerPoints = {
    topLeft: {
      point0: {
        x: leftX,
        y: topY + cornerHeightSize,
      },
      point1: {
        x: leftX,
        y: topY,
      },
      point2: {
        x: leftX + cornerWidthSize,
        y: topY,
      },

    },
    topRight: {
      point0: {
        x: rightX - cornerWidthSize,
        y: topY,
      },
      point1: {
        x: rightX,
        y: topY,
      },
      point2: {
        x: rightX,
        y: topY + cornerHeightSize,
      },
    },
    bottomRight: {
      point0: {
        x: rightX,
        y: bottomY - cornerHeightSize,
      },
      point1: {
        x: rightX,
        y: bottomY,
      },
      point2: {
        x: rightX - cornerWidthSize,
        y: bottomY,
      },
    },
    bottomLeft: {
      point0: {
        x: leftX,
        y: bottomY - cornerHeightSize,
      },
      point1: {
        x: leftX,
        y: bottomY,
      },
      point2: {
        x: leftX + cornerWidthSize,
        y: bottomY,
      },
    },
  };

  _drawCornersOnCanvas(cornerPoints, ctx, scaleWidth, scaleHeight);
};

const _drawCornersOnCanvas = (cornerPoints, ctx) => {
  Object.keys(cornerPoints).forEach((corner) => {
    const cornerPoint = cornerPoints[corner];
    const {point0, point1, point2} = cornerPoint;
    ctx.beginPath();
    ctx.moveTo(point0.x, point0.y);
    ctx.lineTo(point1.x, point1.y);
    ctx.lineWidth = 2;

    ctx.lineTo(point2.x, point2.y);
    ctx.lineWidth = 2;
    ctx.strokeStyle = '#FFFFFF';
    ctx.stroke();
  });
};

export {
  measureImgSize,
  drawViewport,

};
