import { getOpposite } from './geometry';
import { hints } from '../_cvConstants';

const getMessage = (outViewport) => {
  const length = outViewport.length;
  if (!length) {
    return null;
  }

  return hints.general;
};

const getSpecifiedMessageFromOutPoint = (outViewport, lengthOfAllVertices) => {
  const length = outViewport.length;

  if (!length) {
    return null;
  }
  if (length === lengthOfAllVertices) {
    return hints.zoomOut;
  }
  if (length === 1) {
    return hints.center;
  }
  const out = {
    top: 0,
    left: 0,
    bottom: 0,
    right: 0,
  };

  outViewport.forEach((vertex) => {
    const { result } = vertex;
    out.top += Number(result.isTopOut);
    out.left += Number(result.isLeftOut);
    out.bottom += Number(result.isBottomOut);
    out.right += Number(result.isRightOut);
  });

  const entries = Object.entries(out);
  const outsideDefault = { max: -1, message: null };

  const outside = entries.reduce((prev, curr) => {
    const side = curr[0];
    const count = curr[1];
    if (count > prev.max) {
      const opposite = getOpposite(side);
      if (out[opposite] !== 0) {
        return {
          max: count,
          message: hints.center,
        };
      }
      return {
        max: count,
        message: hints[side],
      };
    }
    return prev;
  }, outsideDefault);

  return outside.message;
};

export { getMessage, getSpecifiedMessageFromOutPoint };
