import cx from 'classnames';
import styles from '../CVCamera.scss';
import Button from '/src/Button';
import React from "react";

function CVTakenPhoto({ onUsePhoto, onRetake, img }) {
  const imgSrc = URL.createObjectURL(img);
  const containerStyles = cx(
    styles.riseZIndex,
    styles.shutterBlink
  );

  return (
    <div className={containerStyles}>
      <div className={styles.showTakenPhotoScreen}>
        <img className={styles.takenPhoto} src={imgSrc}/>
        <div className={styles.takenPhotoBtnsFrame}>
          <Button
            onClick={onUsePhoto}
            style={{ width:'80%' }}
          >
          USE PHOTO
          </Button>
          <br/><br/>
          <Button
            onClick={onRetake}
            type="secondary"
            style={{
              color: '#FFAF00',
              width: '80%',
              border: '2px solid #FFAF00',
            }}
          >
          RETAKE PHOTO
          </Button>
        </div>
      </div>
      <div className={styles.underLine} />
    </div>
  );
}

export default CVTakenPhoto;
