import Button from '@material-ui/core/Button';
import React from 'react';
import { render } from 'react-dom';
import {ThemeProvider } from 'styled-components';
import '/lib/opencv/opencv';
import { MobileFileUploader }  from "./src/FileUploader";
import styles from '/src/css/app.scss';
import { createMuiTheme, ThemeProvider as MuiThemeProvider } from '@material-ui/core/styles';
import _get from 'lodash/get'

const theme = {
  color: {
    primary: '#0D74AF',
    secondary: '#127F21',
  }
};

const muiTheme = createMuiTheme({
  palette: {
    primary: { main: '#0D74AF'},
    secondary: { main: '#127F21'}
  }
});

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activateUploader: false,
    }
  }

  render() {
    const path = document && _get(document, ['currentScript', 'src']);
    const src = path && path.replace('.js', '.css');
    return(
      <div className={styles.informedDocAnalyzer}>
        { process.env.NODE_ENV === 'production' && <link rel="stylesheet" type="text/css" href={src} />}
        <ThemeProvider theme={theme}>
          <MuiThemeProvider theme={muiTheme}>
          {/*<CssBaseline />*/}
            <MobileFileUploader ref={(el) => this.mobileFileUploader = el}/>
            <Button variant="contained" color="primary" onClick={() => this.mobileFileUploader.click()}>Upload Documents</Button>
          </MuiThemeProvider>
        </ThemeProvider>
      </div>
    )
  }
}

render(
  <App />,
  document.getElementById('root')
);
