import styles from './Button.scss';
import classNames from 'classnames/bind';
import React from 'react';
import { withTheme } from "styled-components";

const cx = classNames.bind(styles);

const primaryStyle = (propStyle, theme) => ({
  backgroundColor: theme.color.secondary,
  color: 'white',
  ...propStyle,
});

const secondaryStyle = (propStyle, theme) => ({
  backgroundColor: 'white',
  color: theme.color.secondary,
  borderColor: theme.color.secondary,
  ...propStyle,
});

const Index = ({ theme, type = 'primary', onClick, style, onDisabledClick, children, className, disabled = false }) => {
  const isPrimary = type === 'primary';
  return (
    <button
      className={cx('button', type, className, {disabled})}
      style={isPrimary ? primaryStyle(style, theme) : secondaryStyle(style, theme)}
      disabled={disabled}
      onClick={disabled ? onDisabledClick : onClick}
    >
      {children}
    </button>
  );
};

export default withTheme(Index);
