function dataURItoBlob(dataURI, format) {
  const binary = atob(dataURI.split(',')[1]);
  const array = binary.split('').map(str => str.charCodeAt(0));
  return new Blob([new Uint8Array(array)], { type: format });
}

export function compressFile(file, onLoadCallback, options) {
  const { format, maxWidth } = options;
  const img = new Image();
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');
  img.src = URL.createObjectURL(file);

  img.onload = () => {
    const { width, height } = img;
    canvas.width = Math.min(maxWidth, width);
    canvas.height = (height / width) * canvas.width;
    ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
    onLoadCallback(new File([dataURItoBlob(canvas.toDataURL(format, 0.9))], file.name, { type: format }));
  };
}
